
* {
    box-sizing: border-box;
}



a {
    color: #2762eb;
    text-decoration: none;
}

.containerlogin {
    background-color: #fff;
    border-radius: 15px;
    border: 2px solid #7cb7ee;
    width: 1000px;
    max-width: 100%;
    display: flex;
    box-shadow: 0 4px 5px rgba(0,0,0,0.1); 
}

form{
    border-right: 1px solid #ecf2ff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px;
    flex: 1;
}

.form-control{
    margin: 10px 0;
    width: 100%;
}

.label {
    display: inline-block;
    font-weight: bold;
    margin-bottom: 5px;
}

input:not([type='checkbox']) {
    background-color: transparent;
    border: 2px solid #ecf2ff;
    border-radius: 3px;
    font-family: inherit;
    font-size: 14px;
    padding: 10px;
    width: 100%;
}

.checkbox-container {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin-bottom: 15px;
    width: 100%;
}

.checkbox-container label {
    color: #7a7e8c;
    font-weight: normal;
}

.btn {
    background-color: #0b2558;
    border: 2px solid #0b2558;
    border-radius: 5px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: inherit;
    font-weight: bold;
    font-size: 14px;
    padding: 10px;
    margin: 20px 0;
    width: 100%;
}

.btn-ghost {
    background-color: transparent;
    border-color: #ecf2ff;
    color: #141c2c;
}

.btn img {
    margin-right: 5px;
    width: 20px;
    height: 20px;
}

.features {
    flex: 1.5;
    padding: 40px; 
}

.feature {
    padding-left: 30px;
    position: relative;
    margin: 40px 0;
}

.feature i {
    color: #2762eb;
    position: absolute;
    top: 5px;
    left: 0;
}

.feature h3 {
   margin: 0; 
}

.feature p {
    font-size: 14px;
    line-height: 1.8;
    margin: 5px 0;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }
    
    form {
        border-right: 0;
        margin-top: 300px;
    }

}

@media (max-width: 320px) {
    form {
        margin-top: 450px;
    }
}